.main-sm{
    div{
        @include flexbox();
        @include text-style-20();
        width: 100vw;
        height: 30vh;
        padding-left: 8px;
        margin-bottom: 2px;
        color : white;
        text-shadow: 0px 0px 3px rgb(75, 75, 69);
    }
    .sm-about{
        display: block;
        background : url('https://www.notion.so/image/https%3A%2F%2Fs3-us-west-2.amazonaws.com%2Fsecure.notion-static.com%2F4a98a9d8-62b8-4b0c-83ff-db546594b29e%2Fmain-cut.jpg?table=block&id=370a8b3a-2423-4541-997b-c42e830f2f6e&spaceId=5faaf709-5286-406a-bd35-2f81525f2422&width=2000&userId=64132609-ca0a-4879-a67d-995b82c56f1e&cache=v2')
            no-repeat center/cover;
    }
    .sm-menu{
        display: block;
        background : url('https://www.notion.so/image/https%3A%2F%2Fs3-us-west-2.amazonaws.com%2Fsecure.notion-static.com%2F2267dc41-e335-48bb-a304-0eccc12be7ea%2Fmenu-cut.jpeg?table=block&id=c8ae9734-f91c-46cb-b75d-d82f77fc4385&spaceId=5faaf709-5286-406a-bd35-2f81525f2422&width=1830&userId=64132609-ca0a-4879-a67d-995b82c56f1e&cache=v2') 
            no-repeat center/cover;
    }
    .sm-shop{
        display: block;
        background : url('https://www.notion.so/image/https%3A%2F%2Fs3-us-west-2.amazonaws.com%2Fsecure.notion-static.com%2F357b81f7-d34d-400e-806b-006efa2c059e%2Fshop.jpeg?table=block&id=45161b19-517d-4277-b8cd-7bd9d68daf74&spaceId=5faaf709-5286-406a-bd35-2f81525f2422&width=1830&userId=64132609-ca0a-4879-a67d-995b82c56f1e&cache=v2') 
            no-repeat center/cover;
    }
}


@include responsive(lg){
    .main-lg{
        @include flexbox();
        width: 100vw;
        height: 95vh;
        background: url('https://www.notion.so/image/https%3A%2F%2Fs3-us-west-2.amazonaws.com%2Fsecure.notion-static.com%2F541da136-72a7-4e93-985b-82049bdb6bc1%2Fmain.jpg?table=block&id=c9ebbab7-5141-4105-9f84-76bb9b4e1b2d&spaceId=5faaf709-5286-406a-bd35-2f81525f2422&width=2000&userId=64132609-ca0a-4879-a67d-995b82c56f1e&cache=v2')
            no-repeat center/cover;
    }
}